import "./intro.scss"
import { init } from "ityped"
import { useEffect, useRef } from "react"

export default function Intro() {

    const textRef = useRef();

    useEffect(() => {
        init(textRef.current,{
            showCursor:true,
            backDelay :1500,
            backSpeed:60,
            strings:["Student", "Intern"],
            
        })
     }, [])
    return (
        <div className="intro" id= "intro">
            <div className="left">
            <div className="imageContainer">
                <img src="assets/CV-picture2.png" alt=""/>
            </div>
            </div>
            
            <div className="right">
                <div className="wrapper">
                    <h2> Hello World </h2>
                    <h1>I'm Basma Belahcen</h1>
                    <h3>Computer Science <span ref ={textRef}></span></h3>
                </div>
                <a href="#portfolio">
                    <img src="assets/down.png" alt=""/>
                </a>
            </div>
        </div>
    )
}
